/*
 * [page ajax]
 */
import qs from 'qs'
import axios from 'axios'

const service = axios.create({
    timeout: 6e4,
    validateStatus: x => x === 200
})

/**
 * 統一樣式的狀態碼處理
 * @param {any} h 囘調
 * @returns {null} 無
 */
const done = function (h) {
    return this.then(h).catch(({
        code,
        msg
    }) => {
        console.error('請求出錯啦 =>', code, msg)
    })
}

/**
 * 請求發送前處理
 * @param {any} o 配置前
 * @returns {object} 配置後
 */
const onsend = o => {
    // const token = localStorage.getItem('token') || ''
    if (typeof o.data === 'string') {
        o.headers[o.method]['Content-Type'] = 'application/json'
    } else if (Array.isArray(o.data)) {
        const form = new FormData()

        Object.entries(o.data[0]).forEach(([k, v]) => {
            if (Array.isArray(v)) {
                v.forEach(x => form.append(k, x))
            } else {
                form.append(k, v)
            }
        })

        o.data = form
    } else {
        o.data = qs.stringify(o.data)
    }

    // if (token){
    //     o.headers.common.Authorization = token
    // }

    return o
}

/**
 * 請求成功囘調
 * @param {object} o 請求對象
 * @returns {object} 響應
 */
const onsuccess = o => {
    console.log('請求結果 =>', o.data)

    if (o.status !== 200) {
        throw o.status
    }

    // 此處數據有後臺api數據格式決定
    const {
        Code,
        Message,
        Data
    } = o.data

    if (Code === 0) {
        return Data
    }

    throw {
        Code,
        Message
    }
}

/**
 * 請求失敗囘調
 * @param {any} e 錯誤信息
 * @returns {any} 無
 */
const onerror = e => {
    const info = e.toString()
    const err = {
        code: 0,
        msg: '请求失败'
    }

    if (info.includes('Network Error')) {
        err.msg = '网络错误'
    } else if (info.includes('timeout of')) {
        err.msg = '请求超时'
    }

    // console.log('error =>', e)

    throw err
}

Promise.prototype.done = done
window.Promise.prototype.done = done
service.interceptors.request.use(onsend)
service.interceptors.response.use(onsuccess, onerror)

export default {
    $get: (url, params) => service.get(url, {
        params
    }),
    $pop: (url, params) => service.delete(url, {
        params
    }),
    $put: (url, o) => service.put(url, o),
    $post: (url, o) => service.post(url, o),
    $patch: (url, o) => service.patch(url, o),
    $form: (url, o) => service.post(url, [o]),
    $auth: url => service.get(url, {
        responseType: 'blob'
    }).then(o => ({
        key: o.headers.key,
        data: o.data
    }))
}



