 /*
  * @Description: 状态管理入口文件
  */
 import Vue from 'vue'
 import Vuex from 'vuex'

 Vue.use(Vuex)

 export default new Vuex.Store({
     state: {
      noDoctor: '设为主诊医生',
      yesDoctor: '主诊医生', // 点击变为主诊医生
      flag: true
     },
     mutations: {
         add (state) {
             state.count += 1
         },
         reduce (state) {
             state.count -= 1
         }
     },
     actions: {

     },
     getters: {

     }
 })



