import Vue from 'vue'
import Router from 'vue-router'
import VisitConsulting from '@/page/VisitConsulting/index'
import seekDoctor from '@/page/inquiry/seekDoctor'
// 医生主页
import doctorDetails from '@/page/inquiry/doctorDetails'
import shiyan from '@/page/inquiry/shiyan'
// 评价
import evaluate from '@/page/inquiry//evaluate'
// 服务包
import serveBag from '@/page/serveBag/serveBag'
// 填写问题描述
import depictContent from '@/page/depict/depictContent'
// 预约时间
import appointmentTime from '@/page/appointmentTime/appointmentTime'
// 支付
import payment from '@/page/payment/payment'
// 支付成功
import paymentSucceed from '@/page/payment/paymentSucceed'
// 支付失败
import paymentFailure from '@/page/payment/paymentFailure'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/VisitConsulting',
    name: 'VisitConsulting',
    component: VisitConsulting
  }, {
    path: '/seekDoctor',
    name: 'seekDoctor',
    component: seekDoctor,
    meta: {
      title: '找医生',
      noHeader: true
    }
  }, {
    path: '/serveBag',
    name: 'serveBag',
    component: serveBag,
    meta: {
      title: '服务包选择'
    }
  }, {
    path: '/doctorDetails',
    name: 'doctorDetails',
    component: doctorDetails,
    meta: {
      title: '医生主页',
      noHeader: true
    }
  }, {
    path: '/evaluate',
    name: 'evaluate',
    component: evaluate,
    meta: {
      title: '评价'
    }
  }, {
    path: '/depictContent',
    name: 'depictContent',
    component: depictContent,
    meta: {
      title: '问诊'
    }
  }, {
    path: '/appointmentTime',
    name: 'appointmentTime',
    component: appointmentTime,
    meta: {
      title: '预约时间'
    }
  }, {
    path: '/shiyan',
    name: 'shiyan',
    component: shiyan,
    meta: {
      title: '实验'
    }
  }, {
    path: '/payment',
    name: 'payment',
    component: payment,
    meta: {
      title: '订单支付'
    }
  }, {
    path: '/paymentSucceed',
    name: 'paymentSucceed',
    component: paymentSucceed,
    meta: {
      title: '支付成功'
    }
  }, {
    path: '/paymentFailure',
    name: 'paymentFailure',
    component: paymentFailure,
    meta: {
      title: '支付失败'
    }
  }]
})



