import ajax from '../utils/ajax'
const baseUrl = 'http://172.18.30.4/PlatFormAPI/Help'

const getExample = function () {
    return ajax.$get(baseUrl + 'PatientAccount/QueryPatientInfoByPatientId', {PatientId: 1})
}

export default {
    getExample
}



