import store from "@/store/index"
import seekDoctor from "@/api/seekDoctor"
export default {
  data() {
    return {
      button: [
        { flag:false,  evaluate: "全部评价" },
        { flag:false,  evaluate: "好评" },
        { flag:false,  evaluate: "差评" }
      ],
      tabIndex: 0,
      //获取评价数据
      evaluate: []
    }
  },
  methods: {
      evaluateClick(index) {
          this.button[index].flag = !this.button[index].flag
          this.tabIndex = index
      }
  },
  created() {
    var DrId=this.$route.query.id
    console.log(DrId)
    seekDoctor.evaluate(DrId).then(res => {
        console.log(res)
        this.evaluate = res.ReturnData
    })
  }
};



