import ajax from '../utils/ajax'
const baseUrl = 'http://172.18.30.4/PlatFormAPI/'

const getDoctor = function (options) {
    return ajax.$post(baseUrl + 'Doctor/QueryDoctorInfoByPage', {
        HospitalId: 0,
        DrType: 1,
        Position: -1,
        OrgId: -1,
        HosGroupId: -1,
        IsHasPack: -1,
        HospitalLevel: -1,
        PicturePath: '',
        VisitCount: '',
        IsPatientMain: '',
        ...options
    })
}
// 获取ID返回医生信息
const doctorDetail = function (DrId) {
    return ajax.$get(baseUrl + 'Doctor/QueryDoctorInfoByDrId?', {
        DrId: DrId,
        PatientId: -1
    })
}
// 获取科室数据
const Departments = function () {
    return ajax.$post(baseUrl + 'Doctor/QueryDepartmentPage', {
        ParentId: 0,
        PageSize: 500
    })
}
// 获取医院数据
const hospital = function () {
    return ajax.$post(baseUrl + 'Doctor/QueryHospitalPage', {
        HospitalName: ''
    })
}
// 获取筛选数据
const screen = function () {
    return ajax.$post(baseUrl + 'Category/QueryCategoryItem', {
        'CategoryIds': [
            9,
            10
        ]
    })
}
// 获取评价列表
const evaluate = function (DrId) {
    return ajax.$post(baseUrl + 'DoctorPatient/QueryDoctorEvaluationByPage', {
        DrId: DrId
    })
}
// 调用支付接口
const payment = function () {
    return ajax.$post('http://172.18.30.68:6060/Pay/PayOrder', {

    })
}
export default {
    getDoctor,
    doctorDetail,
    Departments,
    hospital,
    screen,
    evaluate,
    payment
}



