
import $ from 'jquery'
import doctorDetail from "@/api/doctorDetail";
import seekDoctor from "@/api/seekDoctor";
import store from "@/store/index";
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
data() {
return {
doctorDetail: {}
};
},
components: {
XHeader
},
store,
created() {
var DrId=this.$route.query.id

seekDoctor.doctorDetail(DrId).then(res => {

this.doctorDetail = res


})
},
methods: {
showPosition(position) {
this.position = position;
this.showPositionValue = true;
this.$store.state.flag = !this.$store.state.flag;
if (this.$store.state.flag == true) {
this.$vux.toast.show({
text: "取消成功"
});
} else {
this.$vux.toast.show({
text: "设置成功"
});
}
}
},
mounted() {
$(".vux-header-title").css({
color: '#fff'
})
}
};



