
import $ from "jquery";
import store from "@/store/index";
import seekDoctor from "@/api/seekDoctor";
import Scroller from 'vux/src/components/scroller/index.vue'

import XHeader from 'vux/src/components/x-header/index.vue'

import { setTimeout } from "timers";

export default {
components: {
Scroller,
XHeader
},
data() {
return {

showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,

flags: false,
flag: false,
tabIndex: 0,
submitIndex: 0,
dieScreen: [
{
title: "医院等级：",
tagList: [
{ id: 0, name: "三级甲等", selected: false },
{ id: 1, name: "二级甲等", selected: false },
{ id: 2, name: "一级甲等", selected: false },
]
},
{
title: "问诊类型",
tagList: [
{ id: 1101, name: "图文问诊", selected: false },
{ id: 1102, name: "视频问诊", selected: false },
{ id: 1103, name: "名义续方", selected: false }
]
},
{
title: "医生职称",
tagList: [
{ id: 0, name: "主任医师", selected: false },
{ id: 1, name: "副主任医师", selected: false },
{ id: 2, name: "主治医师", selected: false }
]
},
{
title: "定制服务包",
tagList: [
{ id: 1, name: "有服务包", selected: false },
{ id: 0, name: "无服务包", selected: false },
]
}
],
downIcon: [
{ sort: "医院", up: true },
{ sort: "科室", up: true },
{ sort: "综合排序", up: true },
{ sort: "筛选", up: true }
],
comprehensiveRanking: [
{ name: "综合排序" },
{ name: "图文问诊量" },
{ name: "视频问诊量" },
{ name: "好评率" },
{ name: "24小时回复率" }
],
nickName: "",

seekDoctor: [],

Departments: [],

hospital: [],

screen: [],
searchVal: "", //默认输入为空
letter: "", //默认不排序
original: false, //默认从小到大排序
rank: null,
timer: ""
};
},

watch: {
nickName(newVal) {
this.timer = null;
this.timer = setTimeout(() => {
let parmas = {
keyName: newVal
};
this.getDoctorList(parmas);
}, 500);
}
},
methods: {
editName() {
this.nickName = "";
},
sort(index) {
if (this.rank === index) {
this.rank = null;
} else {
this.rank = index;
}
},
whiteBgc(index) {
this.tabIndex = index;
},
toggle(fIndex, cIndex) {
let chooseTagList = this.dieScreen[fIndex].tagList;
chooseTagList.forEach((item, tagIndex) => {
if (cIndex === tagIndex) {
item.selected = !item.selected;
} else {
item.selected = false;
}
});
},
getDoctorList(params = {}) {

seekDoctor.getDoctor(params).then(res => {

this.seekDoctor = res.ReturnData;
});
},
getHospital(paramsH = {}) {

seekDoctor.hospital(paramsH).then(res => {

this.hospital = res.ReturnData;
});
},

sumitClick() {
let arr = this.dieScreen.map((item, index) => {
return (
item.tagList.filter((cItem, cIndex) => {
return cItem.selected;
})[0] || {}
);
});
console.log(arr)

let parmas = {
SericeItemCode: arr[1].id || null,
IsHasPack: arr[3].id || -1
}
console.log(parmas)
this.getDoctorList(parmas);
this.rank = 4;
},

HospitalSearch(index) {
this.flag = true;
this.tabIndex = index;

let obj
let arr = this.hospital.forEach((item, itemIndex) => {
if (index == itemIndex) {
obj = item.HospitalId
}
})
console.log(obj)
let parmasH = {
HospitalId: obj || -1
}
console.log(parmasH)
this.getDoctorList(parmasH)
this.rank = 4;
},

resetClick() {
this.dieScreen.forEach((item, index) => {
item.tagList.forEach((cItem, cIndex) => {
cItem.selected = false;
});
});
},

onScroll(pos) {
this.scrollTop = pos.top;
},

reset() {
this.rank = 4;
}
},

computed: {

},
created() {

this.getDoctorList();

seekDoctor.Departments().then(res => {

this.Departments = res.ReturnData;
});

this.getHospital();

seekDoctor.screen().then(res => {

this.screen = res;
});
},
store
};



